export default {
  name: "Alert",
  props: {
    type: {
      type: String,
      default: "info",
      validator(value) {
        // The value must match one of these strings
        return ["info", "warning", "danger"].includes(value);
      },
    },
  },
  computed: {
    isLightMode() {
      return this.$colorMode.value === "light";
    },
  },
};
